<template>
  <div id="app">
    <div class="container">
      <div class="home">
        <header>
          <h1>{{ this.user }}</h1>
        </header>

        <form class="f" enctype="multipart/form-data">
          <!--Nivel-->
          <div class="cardi">
            <div class="horizontal">
              <div>
                <img
                  src="../components/assets/VACIO.png"
                  class="search"
                  v-if="nivelIMG == 0"
                />
                <img
                  src="../components/assets/CASI_VACIO.png"
                  class="search"
                  v-if="nivelIMG == 1"
                />
                <img
                  src="../components/assets/MITAD.png"
                  class="search"
                  v-if="nivelIMG == 2"
                />
                <img
                  src="../components/assets/CASI_LLENO.png"
                  class="search"
                  v-if="nivelIMG == 3"
                />
                <img
                  src="../components/assets/LLENO.png"
                  class="search"
                  v-if="nivelIMG == 4"
                />
                <img
                  src="../components/assets/VACIO_SUCIO.png"
                  class="search"
                  v-if="nivelIMG == 5"
                />
                <button
                  class="button is-primary espacio-izq"
                  @click="openModal(5)"
                >
                  <i class="fa fa-info-circle"></i>
                </button>
              </div>
              <div class="col-xs-6 mr-auto mt-auto mb-auto">
                Nivel actual del tanque.
                <input
                  class="form-control"
                  v-model="nivelTXT"
                  type="text"
                  readonly
                />
              </div>
            </div>
          </div>

          <!--Limpieza-->
          <div class="cardi">
            <div class="horizontal">
              <div>
                <img
                  src="../components/assets/LLENO.png"
                  class="search"
                  v-if="limpio == 1"
                />
                <img
                  src="../components/assets/VACIO_SUCIO.png"
                  class="search"
                  v-else
                />
                <button
                  class="button is-primary espacio-izq"
                  @click="openModal(4)"
                >
                  <i class="fa fa-info-circle"></i>
                </button>
              </div>
              <div class="col-xs-6 mr-auto mt-auto mb-auto">
                Calidad del agua almacenada en el tanque.
                <input
                  class="form-control"
                  v-model="limpioTXT"
                  type="text"
                  readonly
                />
              </div>
            </div>
          </div>

          <!--Milimetros-->
          <div class="cardi">
            <div class="horizontal">
              <div>
                <img src="../components/assets/lluvia.jpg" class="search" />
                <button
                  class="button is-primary espacio-izq"
                  @click="openModal(3)"
                >
                  <i class="fa fa-info-circle"></i>
                </button>
              </div>
              <div class="col-xs-6 mr-auto mt-auto mb-auto">
                Milímetros de precipitación captados en la última lluvia.
                <input
                  class="form-control"
                  v-model="mmTXT"
                  type="text"
                  readonly
                />
              </div>
            </div>
          </div>

          <!--Tanque-->
          <div class="cardi">
            <div class="horizontal">
              <div>
                <img src="../components/assets/Capacidad.png" class="search" />
                <button
                  class="button is-primary espacio-izq"
                  @click="openModal(2)"
                >
                  <i class="fa fa-info-circle"></i>
                </button>
              </div>
              <div class="col-xs-6 mr-auto mt-auto mb-auto">
                Ingrese la capacidad de su tanque en <b>litros</b>.
                <input
                  class="form-control"
                  placeholder="Capacidad del tanque (lts)"
                  v-model="tanque"
                  type="text"
                />
              </div>
            </div>
          </div>

          <!--Captar-->
          <div class="cardi">
            <div class="horizontal">
              <div>
                <img src="../components/assets/techo.jpg" class="search" />
                <button
                  class="button is-primary espacio-izq"
                  @click="openModal(1)"
                >
                  <i class="fa fa-info-circle"></i>
                </button>
              </div>
              <div class="col-xs-6 mr-auto mt-auto mb-auto">
                Ingrese la capacidad del techo que podrá captar agua en
                <b>m<sup>2</sup></b
                >.
                <input
                  class="form-control"
                  placeholder="Capacidad del techo (m2)"
                  v-model="captar"
                  type="text"
                />
              </div>
            </div>
          </div>

          <!--Pluviometro-->
          <div class="cardi">
            <div class="horizontal">
              <div>
                <img src="../components/assets/pl.jpg" class="search" />
                <button
                  class="button is-primary espacio-izq"
                  @click="openModal(0)"
                >
                  <i class="fa fa-info-circle"></i>
                </button>
              </div>
              <div class="col-xs-6 mr-auto mt-auto mb-auto">
                Ingrese la capacidad del pluviómetro en <b>milímetros</b>.
                <input
                  class="form-control"
                  placeholder="Capacidad del p. (mm)"
                  v-model="pluviometro"
                  type="text"
                />
              </div>
            </div>
          </div>

          <!--Actualizar los parámetros-->
          <button
            class="btn button is-primary"
            type="submit"
            @click="actualizar"
            :disabled="
              !captarIsValid || !tanqueIsValid || !pluvIsValid || this.error
            "
          >
            <strong>
              <i class="fa fa-upload espacio-der"></i>
              Cargar datos al servidor
            </strong>
          </button>
        </form>

        <!--No se pudo actualizar la información o existe algún error-->
        <div class="error" v-if="error">
          <hr />
          <div class="notification is-danger">
            {{ error }}
          </div>
        </div>
        <!--Sí pudo-->
        <div class="alert" v-if="good">
          <hr />
          <div class="alert alert-primary alert-dismissible fade show">
            ¡Información registrada con éxito!
          </div>
        </div>
      </div>
    </div>
  </div>
  <dialog-component
    :show="showModal"
    :opc="opcion"
    @close="closeModal"
  ></dialog-component>
</template>

<script>
//importar firebase
import firebase from "firebase/compat/app";
import "firebase/compat/auth";
import "firebase/compat/firestore";
import db from "../firebase/init.js";

//importar la ventana emergente
import DialogComponent from "../components/layout/Modal.vue";

export default {
  components: {
    DialogComponent,
  },
  data() {
    return {
      user: null,
      id: null,
      error: null,
      good: null,
      captar: null,
      limpio: null,
      limpioTXT: null,
      mm: null,
      mmTXT: null,
      nivel: null,
      nivelIMG: null,
      nivelTXT: null,
      pluviometro: null,
      tanque: null,
      showModal: false,
      opcion: null,
    };
  },
  name: "SCALL",
  mounted() {
    this.identificarUsuario();
  },
  methods: {
    //Modal
    openModal(opc) {
      this.showModal = true;
      this.opcion = opc;
    },
    closeModal() {
      this.showModal = false;
    },

    //Trabajar con Firebase
    identificarUsuario() {
      const user = firebase.auth().currentUser;
      if (user) {
        this.user = "SCALL de " + user.displayName;
        this.id = user.uid;
        this.searchUser();
      } else {
        this.error = "No se pudo identificar el usuario";
      }
    },
    searchUser() {
      const collectionRef = db.collection("Usuarios");
      const docRef = collectionRef.doc(this.id);
      docRef
        .get()
        .then((doc) => {
          if (doc.exists) {
            //guarda la información en las variables
            this.captar = doc.data().captar;
            this.limpio = doc.data().limpio;
            this.mm = doc.data().mm;
            this.nivel = doc.data().nivel;
            this.pluviometro = doc.data().pluviometro;
            this.tanque = doc.data().tanque;
            //llama a las funciones auxiliares
            this.textoCalidad();
            this.nivelAgua();
            this.mmDesconocido();
          } else {
            this.error = "El documento no existe";
          }
        })
        .catch((err) => {
          this.nivelIMG = 5;
          this.error = err.message;
        });
    },
    actualizar() {
      //reiniciar los letreros
      this.good = null;
      this.error = null;

      //llamada a las funciones auxiliares
      this.nivelAgua();
      this.mmDesconocido();

      //guardar los datos en el servidor
      const collectionRef = db.collection("Usuarios");
      collectionRef
        .doc(this.id)
        .set({
          captar: this.captar,
          tanque: this.tanque,
          pluviometro: this.pluviometro,
          mm: this.mm,
          nivel: this.nivel,
          limpio: this.limpio,
        })
        .then(() => {
          this.good = "good";
          this.nivelAgua();
          this.mmDesconocido();
        })
        .catch((err) => {
          this.error = err.message;
        });
    },

    //Establecer los valores del SCALL
    textoCalidad() {
      if (this.limpio == 1) {
        this.limpioTXT = "Limpio";
      } else if (this.limpio == 2) {
        this.limpioTXT = "Sucio";
      } else {
        this.limpioTXT = "Desconocido";
      }
    },
    nivelAgua() {
      this.nivelTXT = this.nivel + " litros";
      if (this.nivel > (this.tanque / 4) * 3) {
        this.nivelIMG = 4;
      } else if (this.nivel > this.tanque / 2) {
        this.nivelIMG = 3;
      } else if (this.nivel > this.tanque / 4) {
        this.nivelIMG = 2;
      } else if (this.nivel > 0) {
        this.nivelIMG = 1;
      } else if (this.nivel == 0) {
        this.nivelIMG = 0;
      } else {
        this.nivelIMG = 5;
        this.nivelTXT = "Desconocido";
      }
    },
    mmDesconocido() {
      if (this.mm < 0) {
        this.mmTXT = "Desconocido";
      } else {
        this.mmTXT = this.mm + " milímetros";
      }
    },
  },
  computed: {
    //Validar los campos que debe llenar el usuario
    captarIsValid() {
      return !!this.captar;
    },
    tanqueIsValid() {
      return !!this.tanque;
    },
    pluvIsValid() {
      return !!this.pluviometro;
    },
  },
};
</script>

<style>
@import url(https://stackpath.bootstrapcdn.com/bootstrap/4.1.0/css/bootstrap.min.css);
@import url(https://use.fontawesome.com/releases/v5.0.13/css/all.css);
</style>