<template>
  <transition name="fade">
    <div class="modal-overlay" v-if="show"></div>
  </transition>

  <transition name="fade">
    <div class="home mooodal" v-if="show">
      <header>
        <h1>{{ titles[opc] }}</h1>
      </header>
      <span>{{ descriptions[opc] }}</span>
      <form class="f">
        <button class="btn button is-primary" @click="closeModal">Aceptar</button>
      </form>
    </div>
  </transition>
</template>

<script>
export default {
  props: {
    show: Boolean,
    opc: Number,
  },
  data() {
    return {
      //Del 0 al 5 son para la ventana SCALL
      //El 6 es para la ventana Excedente
      titles: [
        "Pluviómetro",
        "Captación",
        "Tanque",
        "Milímetros captados",
        "Calidad del agua",
        "Nivel del tanque",
        "¡Importante!",
      ],
      descriptions: [
        "El pluviómetro, es el recipiente que se encuentra en la parte superior del SCALL, y es el encargado de cuantificar los milímetros de precipitación que caen en una lluvia. En este apartado debes ingresar su capacidad para poder calcular los milímetros de precipitación de la última lluvia",
        "Ingresa en este apartado, en metros cuadrados, el total de la capacidad del techo que dispones que puede captar agua. Con este dato y con los milímetros de precipitación estimados en el pronóstico del clima, se podrá obtener el cálculo del agua que podrá ser captada, el cual, podrás visualizar en tu aplicación móvil",
        "Ingresa en este apartado, en litros, la capacidad total del agua que puedes almacenar. Si la capacidad de almacenamiento se viera rebasada en base al pronóstico de lluvia, tu aplicación móvil te alertará de esta situación y te desplegará una serie de actividades recomendadas que podrás hacer con el agua excedente, las cuales debiste configurar previamente en el apartado 'Excedente de agua' de esta página.",
        "En este apartado se muestran los milímetros reales de agua que pudiste captar en la última lluvia. Este dato se recopila directamente del pluviómetro de tu SCALL.",
